<script>
$(function() {

        $(".nav .child .is-active").parent().slideDown(300), 
            $(".nav .child .is-active").parents(".has-children").addClass("is-visible"), 
            $(".nav .has-children a").on("click", function(e) {
            $(this).next().slideToggle(300), //$(".child").not($(this).next()).slideUp(300) 
            //$(".has-children").not($(this).parent()).removeClass("is-visible")
            $(this).parent().toggleClass("is-visible")
        })
});
export default {
  name: 'Script',
  data () {
    return {
      msg: 'Keybel 2'
    }
  }
}
</script>
