$(document).ready(function() {
	var windowWidth = $(window).width();
    var windowHeight = $(window).height();
    var offset = 300;
    var scroll_top_duration = 700,
        offset_opacity = 1200,

        $back_to_top = $('.cd-top');
    var lastId,
        topMenu = $("#menu-scroll"),
        topMenuHeight = topMenu.outerHeight() + 15;
        var topDist = $(".style-guide-hero").position();

    $(window).scroll(function () {

        if ($(this).scrollTop() > offset) {
           	$('.style-guide-page-nav').addClass('is-fixed');
            $('.style-guide-page-nav').addClass('z-depth-7-bottom');
           	$('.style-guide-page-nav').css({"top":"0","width":"100%"});
        }else {
          	$('.style-guide-page-nav').removeClass('is-fixed');
            $('.style-guide-page-nav').removeClass('z-depth-7-bottom');  
          	$('.style-guide-page-nav').css({"top":"auto"});
        }
    })
    $(document).on("scroll", onScroll);
		function onScroll(event){
		    var scrollPos = $(document).scrollTop();
		    $('.style-guide-page-nav .tab a').each(function () {
		        var currLink = $(this);
		        var refElement = $(currLink.attr("href"));
		        if (refElement.position().top <= scrollPos && refElement.position().top + refElement.height() > scrollPos) {
		            $('.style-guide-page-nav .tab a').removeClass("is-active");
		            currLink.addClass("is-active");
		        }
		        else{
		            currLink.removeClass("is-active");
		        }
		    });
		}
});




