import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/Home'
import Intro from '@/components/Dashboard/Layout/Intro'
import Contacto from '@/components/Components/Contacto'
import Button from '@/components/Components/Button'
import Colors from '@/components/Components/Colors'
import PageNotFound from '@/components/PageNotFound'
Vue.use(Router)
var router_link = "#42b983";
const router = new Router({
  mode: 'history',
  base: __dirname,
  routes: [
    {
      path: '/',
      component: HelloWorld,
      meta: { title: 'MaterialVue - Hello',
      progress: {
      func: [
            {call: 'color', modifier: 'temp', argument: router_link},
            {call: 'fail', modifier: 'temp', argument: router_link},
            {call: 'location', modifier: 'temp', argument: 'top'},
            {call: 'transition', modifier: 'temp', argument: {speed: '.5s', opacity: '0.6s', termination: 600}}
          ]
        }
      }
    },
    {
      path: '/Home',
      name: 'Home',
      component: Home,
      meta: { title: 'MaterialVue - Home',
      progress: {
        func: [
          {call: 'color', modifier: 'temp', argument: router_link},
          {call: 'fail', modifier: 'temp', argument: router_link},
          {call: 'location', modifier: 'temp', argument: 'top'},
          {call: 'transition', modifier: 'temp', argument: {speed: '.5s', opacity: '0.6s', termination: 600}}
        ]
      }
    }
    },
    {
      path: '/Contacto',
      name: 'Contacto',
      component: Contacto,
      meta: { title: 'MaterialVue - Contacto',
      progress: {
        func: [
          {call: 'color', modifier: 'temp', argument: router_link},
          {call: 'fail', modifier: 'temp', argument: router_link},
          {call: 'location', modifier: 'temp', argument: 'top'},
          {call: 'transition', modifier: 'temp', argument: {speed: '.5s', opacity: '0.6s', termination: 600}}
        ]
      }
     }
    },
    {
      path: '/introducion',
      name: 'Introducin',
      component: Intro,
      meta: { title: 'MaterialVue - Introducin',
      progress: {
        func: [
          {call: 'color', modifier: 'temp', argument: router_link},
          {call: 'fail', modifier: 'temp', argument: router_link},
          {call: 'location', modifier: 'temp', argument: 'top'},
          {call: 'transition', modifier: 'temp', argument: {speed: '.5s', opacity: '0.6s', termination: 600}}
        ]
      }
     }
    },
    {
      path: '/button',
      name: 'Button',
      component: Button,
      meta: { title: 'MaterialVue - Button',
      progress: {
        func: [
          {call: 'color', modifier: 'temp', argument: router_link},
          {call: 'fail', modifier: 'temp', argument: router_link},
          {call: 'location', modifier: 'temp', argument: 'top'},
          {call: 'transition', modifier: 'temp', argument: {speed: '.5s', opacity: '0.6s', termination: 600}}
        ]
      }
     }
    },
    {
      path: '/colors',
      name: 'Colors',
      component: Colors,
      meta: { title: 'MaterialVue - Colors',
      progress: {
        func: [
          {call: 'color', modifier: 'temp', argument: router_link},
          {call: 'fail', modifier: 'temp', argument: router_link},
          {call: 'location', modifier: 'temp', argument: 'top'},
          {call: 'transition', modifier: 'temp', argument: {speed: '.5s', opacity: '0.6s', termination: 600}}
        ]
      }
     }
    },
     { path: "*", 
       component: PageNotFound,
       meta: { title: 'MaterialVue - 404',
        progress: {
          func: [
            {call: 'color', modifier: 'temp', argument: router_link},
            {call: 'fail', modifier: 'temp', argument: router_link},
            {call: 'location', modifier: 'temp', argument: 'top'},
            {call: 'transition', modifier: 'temp', argument: {speed: '.5s', opacity: '0.6s', termination: 600}}
          ]
        }
       }
     }
  ]

})
router.beforeEach((to, from, next) => {
    document.title = to.meta.title
    next()
});
export default router


